@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");
@font-face {
  font-family: "GT Walsheim Bold";
  src: url("/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.eot"); /* IE9*/
  src: url("/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.eot?#iefix") format("embedded-opentype"); /* IE6-IE8 */
  src: url("91e0638dac19aba2539d2b8b2a7981e8.eot?#iefix") format("embedded-opentype"), url("/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.woff2") format("woff2"), url("/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.woff") format("woff"), url("/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.ttf") format("truetype"), url('/src/assets/@font-face/91e0638dac19aba2539d2b8b2a7981e8.svg#GT Walsheim Bold"') format("svg"); /* iOS 4.1- */
}
*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 100%;
}

body {
  line-height: 1.3;
  font-family: "Poppins", sans-serif;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

ul {
  list-style: none;
}

li {
  list-style: none;
}

.dist-cta-section {
  background: url("/frontend/dist/images/distributor.webp") center/cover, no-repeat;
  height: 90vh;
  position: relative;
  display: flex;
  align-items: center;
}
.dist-cta-section::after {
  content: "";
  position: absolute;
  inset: 0;
  background: rgba(0, 0, 0, 0.808);
}
.dist-cta-section .container {
  width: 90%;
  margin: 0 auto;
  position: relative;
  z-index: 1;
  display: grid;
  gap: 4em;
}
.dist-cta-section .container .text-area {
  display: grid;
  gap: 4em;
}
.dist-cta-section .container .text-area .text {
  line-height: 1.7;
  color: #fff;
}
.dist-cta-section .container .text-area .pseudoclass-text {
  position: relative;
}
.dist-cta-section .container .text-area .pseudoclass-text::after {
  content: "";
  position: absolute;
  width: 70%;
  height: 2px;
  top: 125%;
  left: 15%;
  background: #6aba43;
}
.dist-cta-section .container .btn-wrap {
  text-align: center;
}
.dist-cta-section .container .btn-wrap .btn {
  background: #6aba43;
  color: #fff;
  padding: 15px 20px 15px 20px;
  border-radius: 10px;
  font-weight: 500;
}
@media screen and (min-width: 43em) {
  .dist-cta-section .container {
    width: 70%;
  }
  .dist-cta-section .container .text-area .pseudoclass-text::after {
    top: 138%;
  }
}
@media screen and (min-width: 62em) {
  .dist-cta-section .container {
    width: 50%;
  }
  .dist-cta-section .container .text-area {
    text-align: center;
  }
  .dist-cta-section .container .text-area .pseudoclass-text::after {
    top: 138%;
  }
}